<template>
  <span style="margin-right:6px">
    <el-tag
        v-for="(tag,index) in value"
        closable
        style="margin-right: 6px"
        :disable-transitions="false"
        @close="closeTag(index)">
      {{tag.name }}
    </el-tag>
    <el-button type="primary" icon="el-icon-plus" size="small" @click="dialogVisible = true">添加标签</el-button>

    <el-dialog
        title="添加标签"
        :visible.sync="dialogVisible"
        width="30%">
      <div>
        <el-select v-model="tags.one" placeholder="选择一级标签" style="width: 100%;margin-bottom: 20px">
          <el-option label="一级标签" value="1"></el-option>
          <el-option label="一级标签3" value="2"></el-option>
          <el-option label="一级标签4" value="3"></el-option>
        </el-select>
        <el-select v-model="tags.two" placeholder="选择二级标签" style="width: 100%;margin-bottom: 20px">
          <el-option label="二级标签" value="4"></el-option>
          <el-option label="二级标签3" value="5"></el-option>
          <el-option label="二级标签4" value="7"></el-option>
        </el-select>
        <el-select v-model="tags.three" placeholder="选择三级标签" style="width: 100%;margin-bottom: 20px">
          <el-option label="三级标签" value="8"></el-option>
          <el-option label="三级标签3" value="9"></el-option>
          <el-option label="三级标签4" value="10"></el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">关闭</el-button>
        <el-button type="primary" @click="addTag">加入</el-button>
      </span>
      </el-dialog>
  </span>
</template>

<script>
    export default {
        name: 'SelectTags',
        props: {
            value: {
                type: Array,
                default: []
            }
        },
        data: function () {
            return {
                dialogVisible: false,
                tags: {one: '', two: '', three: ''}
            }
        },
        methods: {
          addTag(){
            //判断当前标签是否已经存在
            this.$emit('input', [...this.value,{id:88,name:'添加的新标签'}])
            this.dialogVisible = false
          },
            closeTag(index) {
                let newTags = []
                for (let i = 0; i < this.value.length; i++) {
                    if (i != index) {
                        newTags.push(this.value[i])
                    }
                }
                this.$emit('input', newTags)
            }
        }
    }
</script>

