<style lang="scss" scoped>@import "./css/style.scss";</style>
<template>
    <div id="editor">
        <el-card>
            <el-form class="u-edit-from" :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="160px">
                <el-form-item v-if="isEditor" label="课程包名称：" prop="courseName">
                    <el-input v-model="ruleForm.name" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="包装课程包名称：" placeholder="请输入包装原子课名称">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="教研标记：" prop="teachMark">
                    <div class="tag-list">
                        <el-tag v-for="(n, index) in 4" :key="index">健康护理指导</el-tag>
                    </div>
                </el-form-item>
                <el-form-item label="运营标签：" prop="operateLabel">
                    <SelectTags v-model="selectTags"></SelectTags>
                </el-form-item>
                <el-form-item label="课程分类：" prop="courseSort">
                    <div class="tag-list">
                        <el-tag v-for="(n, index) in 4" :key="index">患教课程</el-tag>
                    </div>
                </el-form-item>
                <el-form-item label="包装课程包介绍：" prop="packCourseInstroduce">
                    <RichText v-model="ruleForm.packCourseInstroduce" :height="200" />
                </el-form-item>

                <el-form-item label="售卖类型：" prop="salesType">
                    <el-select
                            v-model="ruleForm.discountType"
                            clearable
                            placeholder="请选择售卖类型"
                           
                    >
                        <el-option v-for="(v,k) in $Cfg.sellTypes" :key="k" :label="v" :value="k"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="售卖金额：" prop="salesMoney">
                    <el-input v-model="ruleForm.name" placeholder="请输入金额"></el-input>
                </el-form-item>
                <el-form-item label="折扣类型：" prop="discountType">
                    <el-select
                            v-model="ruleForm.discount"
                            clearable
                            placeholder="请选择折扣类型"
                           
                    >
                        <el-option v-for="(v,k) in $Cfg.discountTypes" :key="k" :label="v" :value="k"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="限时免费：" prop="limtFree">
                    <el-radio v-model="limtFree" label="1">是</el-radio>
                    <el-radio v-model="limtFree" label="2">否</el-radio>
                </el-form-item>
                <el-form-item label="限时时间：" prop="limtTime" style="width: 640px">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="请选择开始日期" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                    </el-col>
                    <el-col class="line item-center" :span="2">-</el-col>
                    <el-col :span="11">
                        <el-date-picker placeholder="请选择结束时间" v-model="ruleForm.date2" style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item label="有效天数：" prop="effectiveDays">
                    <el-input v-model="ruleForm.name" placeholder="请输入天数" ></el-input>
                </el-form-item>
                <el-form-item label="上线时间：" prop="uptime">
                    <el-date-picker type="date" placeholder="请选择上线时间" v-model="ruleForm.date1"></el-date-picker>
                </el-form-item>
                <el-form-item label="课程包封面：" class="u-upload-image">
                    <el-upload
                            class="avatar-uploader"
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :show-file-list="false"
                            :on-success="handleCover">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <div>
                    <el-button v-if="isEditor" type="primary">包装完成</el-button>
                    <el-button v-else type="primary">提交</el-button>
                </div>
            </el-form>
            <div class="multiple-title-card">
                <div class="title">原子课部分</div>
                <atom-course/>
            </div>
            <div class="multiple-title-card">
                <div class="title">课程包部分</div>
                <course-pack/>
            </div>
            <div class="multiple-title-card">
                <div class="title">选取试卷</div>
                <Examination/>
            </div>
        </el-card>
    </div>
</template>

<script>
    import SelectTags from '@/components/SelectTags/SelectTags'
    import RichText from '@/components/RichText/RichText'
    import AtomCourse from '../components/atomCourse/Index.vue'
    import CoursePack from '../components/coursePack/Index.vue'
    import Examination from '../components/examination/Index.vue'


    export default {
        data() {
            return {
                isEditor: false,
                ruleForm: {},
                limtFree: '',
                imageUrl: '',
                selectTags: '',
                rules: {
                    courseName: [
                        { required: true }
                    ],
                    teachMark: [
                        { required: true }
                    ],
                    courseSort: [
                        { required: true }
                    ],
                    packCourseInstroduce: [
                        { required: true }
                    ],
                    operateLabel: [
                        { required: true }
                    ],
                    salesType: [
                        { required: true }
                    ],
                    salesMoney: [
                        { required: true }
                    ],
                    discountType: [
                        { required: true }
                    ],
                    limtFree: [
                        { required: true }
                    ],
                    limtTime: [
                        { required: true }
                    ],
                    effectiveDays: [
                        { required: true }
                    ],
                    uptime: [
                        { required: true }
                    ]
                }
            }
        },
        methods: {
            handleCover (res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
        },
        created () {
            this.isEditor = (this.$route.name === 'CoursePackEditor') ? true : false
        },
        components: {
            RichText, SelectTags, AtomCourse, CoursePack, Examination
        }
    }
</script>
